<template>
  <el-row class="certificationWarp">
    <el-col :span="24" class="warp-breadcrum">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item><b></b></el-breadcrumb-item>
        <el-breadcrumb-item></el-breadcrumb-item>
      </el-breadcrumb>
    </el-col>
    <el-col class="toolbar" style="padding-bottom: 0px;padding-top:20px;">
      <el-form :inline="true" >
        <el-form-item>
            <el-input v-model="searchUserContent">
            <el-select v-model="searchUserType" slot="prepend" @change="selectOpt">
              <!-- <el-option label="" value=""></el-option> -->
              <el-option label="" value=""></el-option>
              <el-option label="" value=""></el-option>
              <el-option label="" value=""></el-option>
            </el-select>
          </el-input>
        </el-form-item>
        <el-form-item label="">
          <el-select v-model="searchType">
            <el-option  v-for="item in userTypes"  :key="item.value"  :label="item.label"  :value="item.value"> </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="">
          <el-select v-model="searchState">
            <el-option  v-for="item in states"  :key="item.value"  :label="item.label"  :value="item.value"> </el-option>
          </el-select>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" v-on:click="search"></el-button>
        </el-form-item>
      </el-form>
    </el-col>
    <el-dialog title="" :visible.sync="ifShowBigImg">
      <img :src="bigImgSrc" style="width:350px;height:350px;display:block;margin:auto;">
    </el-dialog>


    <el-table :data="certificationList"  border style="width: 100%">
      <!-- <el-table-column  prop="userId"  width="200px"  align="center"  label=""></el-table-column> -->
      <!-- <el-table-column  prop="trueName"  align="center"  label="+"> 
        <template scope="scope">
            <span>{{scope.row.trueName}}</span><span>{{scope.row.phone}}</span>
        </template>
       </el-table-column> -->
      <el-table-column  prop="trueName"  align="center"  label=""> 
      </el-table-column>
      <el-table-column  prop="phone"  align="center"  label=""> 
      </el-table-column>
      <el-table-column  prop="certification.type"  align="center"  label=""> 
        <template scope="scope">
          <span v-if='scope.row.certification.type === 1'></span>
          <span v-else-if='scope.row.certification.type === 2'></span>
        </template>
       </el-table-column>
      <el-table-column  prop="certification.companyName"  align="center"  label="">  </el-table-column>
      <!-- <el-table-column  align="center"  label="+"> 
        <template scope="scope">
            <span>{{scope.row.certification.part}}</span><span>{{scope.row.certification.workAddress}}</span>
        </template>
      </el-table-column> -->
      <el-table-column  align="center"  label=""> 
        <template scope="scope">
            <span>{{scope.row.certification.part}}</span>
        </template>
      </el-table-column>
      <el-table-column prop="certification.workAddress" align="center"  label=""> 
      </el-table-column>
      <el-table-column  prop="certification.doctorPic"  align="center"  label="" class-name="imgWrap">
        <template scope="scope">
          <el-tooltip class="item" effect="dark" content="" placement="left">
            <img style="width:150px;height:150px;cursor:pointer" :src="certificationList[scope.$index].certification.doctorPic" v-on:click="showBigImg(scope.$index)">
          </el-tooltip>
        </template>
      </el-table-column>
      <el-table-column align="center"  label=""> 
        <template scope="scope">
            <span v-if='scope.row.certification.state === 1'></span>
            <span v-else-if='scope.row.certification.state === 2'></span>
            <span v-else='scope.row.certification.state === 3'></span>
        </template>
      </el-table-column>
      <el-table-column  label=""  align="center" >
        <template scope="scope">
          <span v-if="certificationList[scope.$index].certification.state=='1'">
            <el-button type="text"  v-on:click="pass(scope.$index)"></el-button>
            <el-button type="text"   v-on:click="dontPass(scope.$index)"></el-button>
          </span>
        </template>
      </el-table-column>
    </el-table>
  </el-row>
</template>
<script>
  import global from '../../global/global'
  export default{
    data(){
      return {
        ifShowBigImg:false,
        searchUserContent:"",
        searchUserType:"",
        searchType:"",
        searchState:"",
        certificationList:[],
        bigImgSrc: '',
        selectVal: '',
        userTypes: [
          {value: '',label: ''},
          {value: 1,label: ''},
          {value: 2,label: ''}
        ],
        states:[
          {value: '',label: ''},
          {value: 1,label: ''},
          {value: 2,label: ''},
          {value: 3,label: ''}
        ],
      }
    },
    created(){
      this.search();
    },
    methods: {
      selectOpt(key){
        this.selectVal = key;
        this.searchUserContent = '';
      },
      search:function(){
        var params;
        if(this.selectVal === ""){
          params = {
            phone: this.searchUserContent,
            trueName: '',
            companyName: '',
            type: this.searchType,
            state: this.searchState,
            token: ''
          }
        }else if(this.selectVal === ""){
          params = {
            phone: '',
            trueName: this.searchUserContent,
            companyName: '',
            type: this.searchType,
            state: this.searchState,
            token: ''
          }
        }else if(this.selectVal === ""){
          params = {
            phone: '',
            trueName: '',
            companyName: this.searchUserContent,
            type: this.searchType,
            state: this.searchState,
            token: ''
          }
        }else{
          params = {
            phone: '',
            trueName: this.searchUserContent,
            companyName: '',
            type: this.searchType,
            state: this.searchState,
            token: ''
          }
        }
        global.axiosGetReq('/userCertificationList/list',params).then((res) => {
          if (res.data.callStatus === 'SUCCEED') {
            this.certificationList = res.data.data
          }else{
            this.$message.error('');
          }
        })
      },
      pass:function(index){
        this.$confirm('?', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          this.certificationList[index].certification.state = "2";
          let params = {
            phone: this.certificationList[index].phone,
            state: this.certificationList[index].certification.state,
            failReason: '',
            token: 'f02fa6e1-ddbe-462d-af65-b9ca4f4835e2'
          }
          global.axiosPostReq('/userCertificationList/verify',params).then((res) => {
            if (res.data.callStatus === 'SUCCEED') {
              this.$message({
                type: 'success',
                message: '!'
              });
              this.search();
            }else{
              this.$message({
                type: 'info',
                message: ''
              });
            }
          })
        })
      },
      dontPass:function(index){
        this.$confirm('?', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          this.certificationList[index].certification.state = "3";
          let params = {
            phone: this.certificationList[index].phone,
            state: this.certificationList[index].certification.state,
            failReason: '',
            token: 'f02fa6e1-ddbe-462d-af65-b9ca4f4835e2'
          }
          global.axiosPostReq('/userCertificationList/verify',params).then((res) => {
            if (res.data.callStatus === 'SUCCEED') {
              this.$message({
                type: 'success',
                message: '!'
              });
              this.search();
            }else{
              this.$message({
                type: 'info',
                message: ''
              });
            }
          })
        })
      },
      showBigImg:function(index){
        this.ifShowBigImg = true;
        this.bigImgSrc = this.certificationList[index].certification.doctorPic;
      },
    },
  }
</script>

<style>
  .certificationWarp .el-select .el-input {
      min-width: 110px;
      max-width: 150px;
    }

</style>



