<template>
  <el-row class="userListWrap">
    <el-col :span="24" class="warp-breadcrum">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item><b></b></el-breadcrumb-item>
        <el-breadcrumb-item></el-breadcrumb-item>
      </el-breadcrumb>
    </el-col>

    <el-col :span="24" class="warp-main">
      <!---->
      <el-col  class="toolbar" style="padding-bottom: 0px;">
        <el-form :inline="true" >
          <el-form-item>
            <el-input v-model="searchUserContent">
              <el-select v-model="searchUserStyle" slot="prepend" placeholder="">
                <el-option label="" value=""></el-option>
                <el-option label="" value=""></el-option>
                <el-option label="" value=""></el-option>
              </el-select>
            </el-input>
          </el-form-item>
          <el-form-item label="">
            <el-select v-model="searchisBindSale" placeholder="">
              <el-option  v-for="item in options"  :key="item.value"  :label="item.label"  :value="item.value"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="">
            <el-select v-model="searchtype" placeholder="">
              <el-option  v-for="item in types"  :key="item.value"  :label="item.label"  :value="item.value"> </el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="">
            <el-input v-model="searchSaleName"></el-input>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" v-on:click="search()"></el-button>
          </el-form-item>
        </el-form>
      </el-col>
      <!---->
      <el-table :data="userList"  border style="width: 100%">
        <el-table-column  prop="trueName"  align="center"  label="">  </el-table-column>
        <el-table-column  prop="phone"  align="center"  label=""> </el-table-column>
        <el-table-column  prop="companyName"  align="center"  label="">  </el-table-column>
        <el-table-column  prop="type"  align="center"  label="">
          <template scope="scope">
            <span v-if="scope.row.type==1"></span>
            <span v-else></span>
          </template>
        </el-table-column>
        <el-table-column  prop="isBindSale"  align="center"  label="">
          <template scope="scope">
            <span v-if="scope.row.isBindSale==1"></span>
            <span v-else></span>
          </template>
        </el-table-column>
        <el-table-column  prop="saleName" align="center"  label="">  </el-table-column>
        <el-table-column  label="" align="center">
          <template scope="scope">
            <el-button v-if="scope.row.isBindSale==1"  type="text"  v-on:click="cancelBindSale(scope.$index,scope.row)"></el-button>
            <el-button v-else  type="text"  v-on:click="addBindSale(scope.$index,scope.row)"></el-button>
            <el-button type="text" v-on:click="details(scope.$index,scope.row)"></el-button>
          </template>
        </el-table-column>
      </el-table>
    </el-col>
    <el-dialog title="" :visible.sync="showBindSalAlert">
      <el-form :inline="true" >
        <el-form-item>
            <el-select v-model="bindSaleSearchType" placeholder="">
              <el-option label="" value=""></el-option>
              <el-option label="" value=""></el-option>
            </el-select>
        </el-form-item>
        <el-form-item>
          <el-input v-model="bindSaleSearchCont" placeholder="">
            <el-button slot="append" icon="search" v-on:click="searchSalse()"></el-button>
          </el-input>
        </el-form-item>
      </el-form>
      <el-table height="500"  :data="salesList" border>
        <el-table-column align="center" property="phone" label=""></el-table-column>
        <el-table-column align="center" property="trueName" label="" width="200"></el-table-column>
        <el-table-column align="center" property="hehushu" label="">
          <template scope="scope">
            <el-button type="text" v-on:click="bandThisSale(scope.$index,scope.row)"></el-button>
          </template>
        </el-table-column>
      </el-table>
    </el-dialog>

    <!--  -->
    <el-dialog custom-class="asgagewgf" title="" :visible.sync="showUserDetailInfor">
      <div class="personalInfor">
        <img :src="someOneUserDetails.userPic" alt="">
        <h3></h3>
        <div class="">
          <span>{{someOneUserDetails.userId}}</span>
          <span>{{someOneUserDetails.trueName}}</span>
        </div>
        <div class="">
          <span>{{someOneUserDetails.phone}}</span>
          <span>{{someOneUserDetails.sex}}</span>
        </div>
        <div class="">
          <span>{{someOneUserDetails.birthday}}</span>
          <span>QQ{{someOneUserDetails.qq}}</span>
        </div>
      </div>
      <div class="certification">
        <h3></h3>
        <div class="">
          <span>{{someOneUserDetails.type | calculateUserType}}</span>
          <span>{{someOneUserDetails.companyName}}</span>
        </div>
        <div class="">
          <span>{{someOneUserDetails.part}}</span>
          <span>{{someOneUserDetails.workAddress}}</span>
        </div>
        <div class="">
          <span></span>
          <img style="width:100px;height:100px;display:block" :src="someOneUserDetails.doctorPic" alt="">
        </div>
      </div>
      <h3></h3>
      <el-table :data="someOneUserDetails.bindSales" border>
        <el-table-column align="center" property="bianhao" label="" width="150"></el-table-column>
        <el-table-column align="center" property="name" label="" width="200"></el-table-column>
        <el-table-column align="center" property="phone" label=""></el-table-column>
        <el-table-column align="center" property="time" label=""></el-table-column>
        <el-table-column align="center" property="hehushu" label=""></el-table-column>
      </el-table>
      <h3></h3>
      <el-table :data="someOneUserDetails.Receiver">
        <el-table-column align="center" property="receiverName" label="" width="150"></el-table-column>
         <el-table-column align="center" label="" width="200">
          <template scope="scope">
            <span>{{scope.row.province}}</span>
            <span>{{scope.row.city}}</span>
            <span>{{scope.row.county}}</span>
         </template>
        </el-table-column>
        <el-table-column align="center" property="receiverDetail" label=""></el-table-column>
        <el-table-column align="center" property="receiverPhone" label="/"></el-table-column>
      </el-table>
    </el-dialog>
  </el-row>
</template>
<script>

  export default{
    data(){
      return {
        needBindSaleUserIndex:null,
        needBindUserPhone:null,
        showBindSalAlert:false,
        showUserDetailInfor:false,
        searchUserContent:null,
        searchUserStyle: "",
        searchtype:'',
        searchisBindSale:"",
        searchSaleName:null,
        bindSaleSearchType:'',
        bindSaleSearchCont:null,
        options: [{
            value: '1',
            label: ''
          }, {
            value: '2',
            label: ''
          }, {
            value: '3',
            label: ''
          },],
          isBindSale: "",
          types: [{
            value: '1',
            label: ''
          }, {
            value: '2',
            label: ''
          }, {
            value: '3',
            label: ''
          },],

        userList:[],
        someOneUserDetails:{
          bindSales:[],
          address:[]
        },
        salesList:[]
      }
    },
    created:function(){
      var that = this;
      that.getUserList();
      that.getSalesList();
    },
    filters: {
      calculateUserType:function(data){
        var str = "";
        if(data==1){
          str = "";
        }else{
          str = "";
        }
        return str;
      }
    },
    methods: {
      getUserList:function(){
        var that = this;
        var obj = {
          // token:that.global.getToken()
          phone:"",
          trueName:"",
          companyName:"",
          isBindSale:"",
          type:"",
          saleName:"",
          token:"111"
        };
        that.global.axiosGetReq('/userManageList/userlist',obj)
        .then((res) => {
          if (res.data.callStatus === 'SUCCEED') {
            that.userList = res.data.data;
          } else {
            that.$message.error('');
          }
        })
      },
      getSalesList:function(){
        var that = this;
        var obj = {
          salePhone:'',
          saleName:'',
          token:"111"
        };
        that.global.axiosGetReq('/userManageList/salelist',obj)
        .then((res) => {
          console.log(res,"saleList");
          if (res.data.callStatus === 'SUCCEED') {
            that.salesList = res.data.data;
          } else {
            that.$message.error('');
          }
        })
      },
      getOneUserDetails:function(userPhone){
        var that = this;
        var obj = {
          phone:userPhone,
          token:"1111"
        };
        that.global.axiosGetReq('/userManageList/detail',obj)
        .then((res) => {
          if (res.data.callStatus === 'SUCCEED') {
            console.log(res,"getOneUserDetails")
            that.someOneUserDetails = res.data.data;
            var boj2 = {
              name:that.someOneUserDetails.saleName,
              phone:that.someOneUserDetails.salePhone,
              time:that.someOneUserDetails.saleCreated,
              hehushu:that.someOneUserDetails.bindUserNum,
            };
            that.someOneUserDetails.bindSales = [];
            that.someOneUserDetails.bindSales.push(boj2);
          } else {
            that.$message.error('');
          }
        })
      },
      bandThisSale:function(index,one){
        var that = this;
        var obj = {
          token:"1211",
          // token:that.global.getToken()
          salePhone:one.phone,
          userPhone:that.needBindUserPhone
        };
        that.global.axiosGetReq('/userManageList/bind',obj)
        .then((res) => {
          if (res.data.callStatus === 'SUCCEED') {
            that.userList[that.needBindSaleUserIndex].isBindSale = "";
            that.userList[that.needBindSaleUserIndex].saleName = that.salesList[index].name;
            that.showBindSalAlert = false;
          } else {
            that.$message.error('');
          }
        })
      },
      addBindSale:function(index,one){
        var that = this;
        that.needBindUserPhone = one.phone;
        that.needBindSaleUserIndex = index;
        that.showBindSalAlert = true;
      },
      details:function(index,user){
        var that = this;
        that.showUserDetailInfor = true;
        that.getOneUserDetails(user.phone);
      },
      searchSalse:function(){
        // bindSaleSearchType
        // that.bindSaleSearchCont
        var that = this;
        var obj = {
          token:"111"
        };
        if(that.bindSaleSearchCont){
          if(that.bindSaleSearchType == ""){
            obj.salePhone = that.bindSaleSearchCont;
          }else{
            obj.saleName = that.bindSaleSearchCont;
          }
          that.global.axiosGetReq('/userManageList/salelist',obj)
          .then((res) => {
            if (res.data.callStatus === 'SUCCEED') {
              that.salesList = res.data.data;
            } else {
              that.$message.error('');
            }
          })
        }else{
          this.$alert('', {confirmButtonText: '',});
        }
      },
      search:function(){
        var flag = true;
        var that = this;
        var obj = {
          saleName:that.searchSaleName,
          token:"111"
        };
        // 
        if(that.searchUserStyle==""){
          obj.phone = that.searchUserContent;
          obj.trueName = "";
          obj.companyName = "";
        }else if(that.searchUserStyle==""){
          obj.phone = "";
          obj.trueName = "";
          obj.companyName = that.searchUserContent;
        }else if(that.searchUserStyle==""){
          obj.phone = "";
          obj.trueName = that.searchUserContent;
          obj.companyName = "";
        }
        //
        if(that.searchtype==""){
          obj.type = "";
        }else if(that.searchtype==""){
          obj.type = 1;
        }else if(that.searchtype==""){
          obj.type = 2;
        }
        //
        if(that.searchisBindSale==""){
          obj.isBindSale = "";
        }else if(that.searchisBindSale==""){
          obj.isBindSale = 1;
        }else if(that.searchisBindSale==""){
          obj.isBindSale = 2;
        }
        if(!that.searchSaleName){
          that.searchSaleName = "";
        }
        // console.log(obj)
          that.global.axiosGetReq('/userManageList/userlist',obj)
          .then((res) => {
            // console.log(res,"searchList")
            if (res.data.callStatus === 'SUCCEED') {
              that.userList = res.data.data;
              //
              // that.searchSaleName = "";
              // that.searchUserContent = "";
            } else {
              that.$message.error('');
            }
          })
      },
      cancelBindSale:function(index,one){
        var that = this;
        that.$confirm('?', {  confirmButtonText: '',  cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          var obj = {
            token:"1211",
            // token:that.global.getToken()
            salePhone:one.salePhone,
            userPhone:one.phone
          };
          that.global.axiosGetReq('/userManageList/disBind',obj)
          .then((res) => {
            if (res.data.callStatus === 'SUCCEED') {
              that.userList[index].isBindSale = "";
              that.userList[index].saleName = "";
              that.$message({
                type: 'success',
                message: '!'
              });
            } else {
              that.$message.error('');
            }
          })
        }).catch(() => {
          that.$message({
            type: 'info',
            message: ''
          });
        });
      },
    },
  }
</script>

<style>
.userListWrap .el-select .el-input {
    width: 110px;
  }
.userListWrap   .asgagewgf h3{
    font-weight: 500;
    line-height: 50px;
    color: #20a0ff;
  }
.userListWrap   .asgagewgf .personalInfor{
    position: relative;
  }
.userListWrap   .asgagewgf .personalInfor img{
    position: absolute;
    top: 0;
    right: 0;
    width: 150px;
    height: 150px;
  }
.userListWrap   .asgagewgf  span{
    width: 40%;
    display: inline-block;
    line-height: 35px;
  }
  /*.demo-table-expand label {
    font-weight: bold;
  }
  .warp  .warp-main .el-input{
    width: 100%;
    padding-right: 0px;
  }
  .warp .el-input{
    width: 70%;
    padding-right: 20px;
  }*/
</style>



