<template>
  <el-row class="userListWrap">
    <el-col :span="24" class="warp-breadcrum">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item><b></b></el-breadcrumb-item>
        <el-breadcrumb-item></el-breadcrumb-item>
      </el-breadcrumb>
    </el-col>

    <el-col :span="24" class="warp-main">
      <!---->
      <el-col  class="toolbar" style="padding-bottom: 0px;">
        <el-form :inline="true" >
          <el-form-item>
            <el-input v-model="searchUserContent" placeholder=''></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-select v-model="searchType" placeholder="">
              <el-option label="" value=""></el-option>
              <el-option label="" value="0"></el-option>
              <el-option label="" value="1"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" v-on:click="search"></el-button>
          </el-form-item>
        </el-form>
      </el-col>

      <!---->
      <el-table :data="getMoneyList" border style="width: 100%">
        <!-- <el-table-column prop="userId" align="center" label=""></el-table-column> -->
        <el-table-column prop="realName" align="center" label=""></el-table-column>
        <el-table-column prop="phone" align="center" label=""></el-table-column>
        <el-table-column prop="cashMoney" align="center" label=""></el-table-column>
        <el-table-column prop="type" align="center" label=""></el-table-column>
        <el-table-column prop="accountUser" align="center" label=""></el-table-column>
        <el-table-column prop="bank" align="center" label=""></el-table-column>
        <el-table-column prop="zhufubaoCount" align="center" label=""></el-table-column>
        <el-table-column prop="appTime" align="center" label=""></el-table-column>
        <el-table-column prop="cashState" align="center" label="">
          <template scope="scope">
            <span v-if="scope.row.cashState == 1"></span>
            <span v-if="scope.row.cashState == 0"></span>
          </template>
        </el-table-column>
        <el-table-column label="" align="center">
          <template scope="scope">
            <span v-if="scope.row.cashState == 0">
              <el-button type="text" v-on:click="passThisGet(scope)"></el-button>
              <!-- <el-button type="text"   v-on:click="dotPassThisGet(scope.$index)"></el-button> -->
            </span>
          </template>
        </el-table-column>
      </el-table>
    </el-col>


  </el-row>
</template>
<script>
  export default{
    data(){
      return {
        searchUserContent:null,
        searchType: '',
        state: '',
        getMoneyList: [],
      }
    },
    created: function() {
      var that = this;
      that.getAllMoney();
    },
    methods: {
      //
      getAllMoney: function() {
        var that = this;
        that.global.axiosGetReq('/witManage/query').then((res) => {
          if (res.data.callStatus === 'SUCCEED') {
            that.getMoneyList = res.data.data;
            console.log(that.getMoneyList,'222');
          } else {
            that.$message.error('');
          }
        })
      },
      //
      search:function(){
        var that = this;
        if (that.searchType == '') {
          that.state = '';
        }else if (that.searchType == 1) {
          that.state = 1
        }else if (that.searchType == 0) {
          that.state = 0
        }
        var obj = {
          state: that.state,
          message: '',
        }
        that.global.axiosPostReq('/witManage/query',obj).then((res) => {
          if (res.data.callStatus === 'SUCCEED') {
            that.getMoneyList = res.data.data;
            console.log(that.getMoneyList,'222');
          } else {
            that.$message.error('');
          }
        })
      },
      //
      passThisGet:function(scope){
        var that = this;
        that.$confirm('?', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          var obj = {
            cashId: scope.row.cashId,
          }
          that.global.axiosGetReq('/witManage/oper',obj).then((res) => {
            if (res.data.callStatus === 'SUCCEED') {
              that.getAllMoney();
              that.$message({
                type: 'success',
                message: '!'
              });
            } else {
              that.$message.error('');
            }
          })
        }).catch(() => {
          that.$message({
            type: 'info',
            message: ''
          });
        });
      },
    },
  }
</script>

<style>
.userListWrap .el-select .el-input {
    width: 110px;
  }
.userListWrap   .asgagewgf h3{
    font-weight: 500;
    line-height: 50px;
    color: #20a0ff;
  }
.userListWrap   .asgagewgf .personalInfor{
    position: relative;
  }
.userListWrap   .asgagewgf .personalInfor img{
    position: absolute;
    top: 0;
    right: 0;
    width: 150px;
    height: 150px;
  }
.userListWrap   .asgagewgf  span{
    width: 40%;
    display: inline-block;
    line-height: 35px;
  }
  /*.demo-table-expand label {
    font-weight: bold;
  }
  .warp  .warp-main .el-input{
    width: 100%;
    padding-right: 0px;
  }
  .warp .el-input{
    width: 70%;
    padding-right: 20px;
  }*/
</style>



