<template>
  <div class="">
    <div class="goodIntroduce">
      <div>
        <span>{{instruction.addName}}</span>
        <span>{{instruction.unit}}</span>
        <span>AQUAPICK</span>
      </div>
      <div>
        <span>{{instruction.registerId}}</span>
        <span>{{instruction.itemPacking}}</span>
        <span></span>
      </div>
      <div>
        <span>/{{instruction.registerDate}}</span>
        <span>{{instruction.storeItemId}}</span>
        <span>{{instruction.remark}}</span>
      </div>
      <div>
        <span>{{instruction.itemRange}}</span>
        <span>{{instruction.producePompany}}</span>
      </div>
    </div>
    <div v-html="instruction.itemDesc"></div>
  </div>
</template>

<script>

  export default {
    name: 'goodIntroduce',
    data () {
      return {
      }
    },
    props:["instruction"]
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.goodIntroduce{
  margin-top:50px;
  border:1px solid #dcdcdc;
  border-radius: 2px;
  /*padding: 0 20px;*/
  padding-left: 50px;
  padding-top: 35px;
  padding-bottom: 38px;
  margin-left: 37px;
  margin-right: 46px;
}
.goodIntroduce div{
  line-height: 45px;
}
.goodIntroduce div span{
  display:inline-block;
}
.goodIntroduce div span:nth-child(1){
  width:50%;
}
.goodIntroduce div span:nth-child(2){
  width:30%;
}
.goodIntroduce div span:nth-child(3){
  width:18%;
}
</style>



