<template>
  <el-row class="incomeWrap">
    <el-col :span="24" class="warp-breadcrum">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item><b></b></el-breadcrumb-item>
        <el-breadcrumb-item></el-breadcrumb-item>
      </el-breadcrumb>
    </el-col>

    <el-col  class="toolbar" style="padding-bottom: 0px;padding-top:20px;">
      <el-form :inline="true" >
        <el-form-item>
          <el-input v-model="searchSaleContent">
            <el-select v-model="searchSaleType" slot="prepend" >
              <el-option label="" value=""></el-option>
              <el-option label="" value=""></el-option>
            </el-select>
          </el-input>
        </el-form-item>
        <el-form-item label="7">
          <el-select v-model="searchGetType">
            <el-option label="" value=""></el-option>
            <el-option label="" value="1"></el-option>
            <el-option label="" value="2"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="">
          <el-select v-model="searchMoneyType">
            <el-option label="" value=""></el-option>
            <el-option label="" value="1"></el-option>
            <el-option label="" value="2"></el-option>
            <el-option label="" value="3"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="">
          <el-date-picker v-model="searchDataPrev" type="daterange" placeholder=""></el-date-picker>
        </el-form-item>
        <!-- <el-form-item label="~">
          <el-date-picker  v-model="searchDataNext"  type="date"  placeholder=""  :picker-options="pickerOptions1"> </el-date-picker>
        </el-form-item> -->
        <el-form-item>
          <el-button type="primary" v-on:click="search"></el-button>
        </el-form-item>
      </el-form>
      <!-- <el-form :inline="true" >

    </el-form> -->
  </el-col>
  <el-table :data="getMoneyList"  border style="width: 100%">
    <!-- <el-table-column  prop="id"  width="200px"  align="center"  label=""></el-table-column> -->
    <el-table-column prop="saleName" align="center" label=""></el-table-column>
    <el-table-column prop="salePhone" align="center" label=""></el-table-column>
    <el-table-column prop="saleId" align="center" label=""></el-table-column>
    <el-table-column prop="orderVoList.orderState" align="center" label="">
      <template scope="scope">
        <span v-if="scope.row.getState == 0"></span>
        <span v-if="scope.row.getState == 1"></span>
        <span v-if="scope.row.getState == 2"></span>
        <span v-if="scope.row.getState == 3"></span>
        <span v-if="scope.row.getState == 4"></span>
        <span v-if="scope.row.getState == 5"></span>
        <span v-if="scope.row.getState == 6"></span>
      </template>
    </el-table-column>
    <el-table-column prop="orderTime" align="center" label=""></el-table-column>
    <el-table-column prop="signLateSeven" align="center" label="7"></el-table-column>
    <el-table-column prop="getMoey" align="center" label=""></el-table-column>
    <el-table-column prop="getState" align="center" label="">
      <template scope="scope">
        <span v-if="scope.row.getState == 1"></span>
        <span v-if="scope.row.getState == 2"></span>
        <span v-if="scope.row.getState == 3"></span>
      </template>
    </el-table-column>
    <el-table-column prop="getUpdated" align="center" label=""></el-table-column>
    <el-table-column align="center" label="">
      <template scope="scope">
        <el-button v-on:click="incomeList(scope)"></el-button>
      </template>
    </el-table-column>
  </el-table>


  <el-dialog custom-class="asgagewgf" title="" :visible.sync="showIncomeInfor">
    <div class="personalInfor">
      <h3></h3>
      <div class="">
        <span>{{someOneUserDetails.saleId}}</span>
        <span>{{someOneUserDetails.saleName}}</span>
        <span>{{someOneUserDetails.salePhone}}</span>
      </div>
    </div>
    <div class="certification">
      <h3></h3>
      <div class="">
        <span>{{someOneUserDetails.userId}}</span>
        <span>{{someOneUserDetails.userName}}</span>
        <span>{{someOneUserDetails.userPhone}}</span>
      </div>
    </div>
    <h3></h3>
    <div class="ordertable">
      <table class="datail_tb">
        <tr><td colspan="7"><span class="pad_l_30">2017-06-08</span><span class="pad_l_30"></span></td></tr>
        <tr class="trs">
          <td></td>
          <td></td>
          <td></td>
          <td></td>
          <td></td>
          <td></td>
          <td></td>
        </tr>
        <tr class="trs" v-for="(item, index) in infoList" :key="index">
          <td>{{item.itemName}}</td>
          <td>{{item.price}}</td>
          <td>{{item.num}}</td>
          <td>{{item.price*item.num}}</td>
          <td :rowspan="infoList.length" v-if="index == 0">{{item.refundMoney}}</td>
          <td :rowspan="infoList.length" v-if="index == 0">{{item.signUpdated}}</td>
          <td :rowspan="infoList.length" v-if="index == 0">{{item.totalFee}}</td>
        </tr>
      </table>
    </div>
      <!-- <el-table :data="someOneUserDetails.bindUsers" border>
        <el-table-column align="center" property="bianhao" label="" width="150"></el-table-column>
        <el-table-column align="center" property="phone" label=""></el-table-column>
        <el-table-column align="center" property="name" label="" width="200"></el-table-column>
        <el-table-column align="center" property="componey" label=""></el-table-column>
      </el-table> -->
    </el-dialog>
  </el-row>
</template>
<script>
  import util from '../../../common/util'
  export default{
    data(){
      return {
        someOneUserDetails:{},
        getMoneyList:[],
        pickerOptions0: {
          disabledDate(time) {
            return time.getTime() < Date.now() - 8.64e7;
          }
        },
        pickerOptions1: {
          disabledDate(time) {
            return time.getTime() < Date.now() - 8.64e7;
          }
        },
        infoList: [{
          goodsName: '1',
          price: 30,
          num: 2,
          totalPrice: 60
        },{
          goodsName: '2',
          price: 30,
          num: 2,
          totalPrice: 60
        },{
          goodsName: '3',
          price: 30,
          num: 2,
          totalPrice: 60
        }],
        showIncomeInfor:false,
        searchSaleContent: '',
        searchDataPrev:[],
        searchDataNext:null,
        searchSaleType:"",
        searchMoneyType: "",
        searchGetType: "",
        nowUserMoneyNum:null,
        // selectSearchType:"",
        showChangeUserMoney:false,
      }
    },
    created: function() {
      var that = this;
      that.getAllIn();
    },
    methods: {
      //
      getAllIn: function() {
        var that = this;
        var obj = {
          saleId: '',
          saleName: '',
          salePhone: '',
          orderId: '',
          signLateSeven : '',
          getState: '',
          startDate: '',
          endDate: '',
          currentPage: '',
          numberPerPage: '',
          token: ''
        }
        that.global.axiosGetReq('/saleIncomeList/query',obj).then((res) => {
          if (res.data.callStatus === 'SUCCEED') {
            that.getMoneyList = res.data.data;
            that.infoList = res.data.data.orderVoList
            console.log(that.getMoneyList,'222');
          } else {
            that.$message.error('');
          }
        })
      },
      incomeList:function(scope){
        var that = this;
        console.log(scope.row,'scope');
        that.someOneUserDetails = scope.row;
        that.showIncomeInfor = true;
      },
      search:function(){
        var that = this;
        if (that.searchDataPrev.length == 0) {
          var startDate = ''
          var endDate = ''
        } else {
          var startDate = util.formatDate.format(new Date(that.searchDataPrev[0]));
          var endDate = util.formatDate.format(new Date(that.searchDataPrev[1]));
        }
        if (that.searchSaleType == '') {
          var salePhone = that.searchSaleContent
          var saleName = ''
        } else {
          var saleName = that.searchSaleContent
          var salePhone = ''
        }
        var obj = {
          saleName: saleName,
          salePhone: salePhone,
          signLateSeven : that.searchGetType,
          getState: that.searchMoneyType,
          startDate: startDate,
          endDate: endDate,
          token: ''
        }
        that.global.axiosPostReq('/saleIncomeList/query',obj).then((res) => {
          if (res.data.callStatus === 'SUCCEED') {
            that.getMoneyList = res.data.data;
            console.log(that.getMoneyList,'222');
          } else {
            that.$message.error('');
          }
        })
      },
      changeUserMoney:function(){
        this.showChangeUserMoney = true;
      },
      saveUserMoney:function(){
        this.showChangeUserMoney = false;
      },
    },
  }
</script>

<style>

  .incomeWrap   .asgagewgf h3{
    font-weight: 500;
    line-height: 50px;
    color: #20a0ff;
  }
  .incomeWrap .el-select .el-input{
    width: 120px;
  }
  .incomeWrap   .asgagewgf .personalInfor{
    position: relative;
  }
  .incomeWrap   .asgagewgf .personalInfor img{
    position: absolute;
    top: 0;
    right: 0;
    width: 150px;
    height: 150px;
  }
  .incomeWrap   .asgagewgf  span{
    width: 30%;
    display: inline-block;
    line-height: 35px;
  }
  .incomeWrap .asgagewgf .ordertable span{
    display: inline-block;
    width: auto;
  }
  .incomeWrap .asgagewgf .ordertable td{
    line-height: 30px;
    padding-left: 10px;
  }
  .incomeWrap .asgagewgf .ordertable .maniCont{
    text-align: center;
  }
  .incomeWrap .asgagewgf .ordertable  .lastTr{
    color: red;
  }
  .incomeWrap .asgagewgf .ordertable  .lastTr td:nth-child(2){
    text-align: right;
    padding-right: 10px;
  }
  .datail_tb{
    width: 100%;
    border-collapse: collapse;/*   */
    border: none;
    margin-top: 20px;
  }
  .datail_tb tr{
    width: 100%;
    height: 30px;
    line-height: 30px;
    border: 1px solid #ccc;
  }
  .datail_tb td{
    width: 100px;
    text-align: center;
    border: 1px solid #ccc;
  }
</style>



